body, html {
  height: 100%;
}

body {
  font-family: sans-serif;
  margin: 0;
}
body #bg {
  clip-path: polygon(100% 0, 100% 82%, 45% 100%, 0 100%, 0 0);
  background-color: #005DFF;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}
@media (min-width: 840px) {
  body #bg {
    clip-path: polygon(0 0, 75% 0, 55% 100%, 0% 100%);
  }
}
body header a {
  color: #fff;
  text-decoration: none;
  padding: 15px;
  display: block;
  text-transform: uppercase;
}

@media (min-width: 840px) {
  main {
    display: grid;
    grid-template-columns: 50% auto;
    grid-template-areas: "primary card";
  }
}
main section#card {
  background-color: #fff;
  padding: 20px;
  margin: 4rem auto;
  width: 80%;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}
@media (min-width: 840px) {
  main section#card {
    grid-area: card;
    height: fit-content;
    align-self: center;
    margin: 1rem;
  }
}
main section#card ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
main section#card ul li {
  margin-bottom: 10px;
}
main section#card ul li span {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #ccdfff;
  border-radius: 50%;
  margin-right: 10px;
}
main section#card ul li strong {
  display: inline-block;
  margin-left: 40px;
  margin-top: 10px;
}
main section#primary {
  color: #fff;
  padding: 15px;
  text-align: center;
}
@media (min-width: 840px) {
  main section#primary {
    grid-area: primary;
    text-align: left;
    margin: 4rem 0 0 4rem;
  }
}
main section#primary h1 {
  font-size: 3rem;
  margin-top: 10px;
  text-transform: uppercase;
}
@media (min-width: 840px) {
  main section#primary h1 {
    width: 30%;
    font-size: 4rem;
    line-height: 0.9;
  }
}
main section#primary p {
  font-size: 1.4rem;
}
main section#primary a {
  color: #001333;
  background-color: #FFF6BB;
  border-radius: 15px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  display: block;
  text-align: center;
  margin: 50px 0 auto 0 auto;
  padding: 15px;
}
@media (min-width: 840px) {
  main section#primary a {
    display: inline-block;
    padding: 15px 60px;
  }
}